html{
  background-color:black;
}
.header {
  height: 100%;
  min-height: 100vh;
  width: 100vw;
  min-width: 100%;
  overflow: hidden;
  color: white;
  background-color: black;
}

.header-contact-links {
  z-index: 2;
  position: absolute;
  bottom: 15px;
  width: 100%;
  padding: 0 20%;
  display: grid;
  grid-template-columns: repeat(7, auto);
  text-align: center;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
#down-arrow-header {
  width: 48px;
  margin: 0 auto;
}
.contact_card_link:nth-child(even) {
  animation: appear_in 6s, mover 1s infinite alternate ease;
  -webkit-animation: appear_in 6s, mover 1s infinite alternate ease;
}
.contact_card_link:nth-child(odd) {
  animation: appear_in 6s, mover 1s infinite alternate-reverse ease;
  -webkit-animation: appear_in 6s, mover 1s infinite alternate-reverse ease;
}

.header-contact-links > span {
  margin: auto;
  box-shadow: inset 0 0 10px #f4f4f4;
}
@-webkit-keyframes appear_in {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes mover {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes appear_in {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes mover {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

.header-text {
  position: absolute;
  text-align: center;
  width: 80%;
  margin: 0 10%;
  top: 5vh;
  z-index: 1;
  padding: auto;
  animation: appear_in 3s;
  -webkit-animation: appear_in 3s;
}
.header-text > h1,h2{
  font-size: 5em;
}
.header-text h2 {
  /* font-family: 'Brush Script MT', cursive; */
  font-family: "Dancing Script";
  text-transform: capitalize;
  font-weight: bold;
}
.header-text p {
  color: #fff;
  font-size: 1.3em;
  font-weight: 1.2em;
}
.header-text h1 {
  font-family: "Berkshire Swash", sans-serif;
  text-transform: capitalize;
  margin-top: 20vh;
}
