.slideshow {
  list-style: none;
  /* z-index: 1; */
  margin: 0;
  width: 100vw;
  height: 100vh;
  min-height: 100%;
  min-width: 100%;
}
.slideshow li span {
  width: 100vw;
  height: 100vh;
  min-height: 100%;
  min-width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  color: transparent;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: none;
  opacity: 0;
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-animation: imageAnimation 12s linear infinite 0s;
  animation: imageAnimation 12s linear infinite 0s;
}
.slideshow li:nth-child(1) span {
  background-image: url(/static/media/landing_image1.cbef4a2e.jpg);
}

.slideshow li:nth-child(2) span {
  background-image: url(/static/media/landing_image2.df6c926b.jpg);
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
}

.slideshow li:nth-child(3) span {
  background-image: url(/static/media/landing_image3.99a08570.jpg);
  -webkit-animation-delay: 6s;
  animation-delay: 6s;
}

.slideshow li:nth-child(4) span {
  background-image: url(/static/media/landing_image4.1772b4c8.jpg);
  -webkit-animation-delay: 9s;
  animation-delay: 9s;
}
.slideshow li {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

@-webkit-keyframes imageAnimation {
  0% {
    opacity: 0;
    -webkit-animation-timing-function: ease-in;
  }
  12.5% {
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
  }
  25% {
    opacity: 1;
  }
  37.5% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes imageAnimation {
  0% {
    opacity: 0;
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  12.5% {
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  25% {
    opacity: 1;
  }
  37.5% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.no-cssanimations .slideshow li span {
  opacity: 1;
}

.chevron {
  position: absolute;
  width: 38px;
  height: 15px;
  opacity: 0;
  -webkit-transform: scale3d(0.5, 0.5, 0.5);
          transform: scale3d(0.5, 0.5, 0.5);
  -webkit-animation: move 3s ease-out infinite;
          animation: move 3s ease-out infinite;
}

.chevron:first-child {
  -webkit-animation: move 3s ease-out 1s infinite;
          animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  -webkit-animation: move 3s ease-out 2s infinite;
          animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: " ";
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #fff;
}

.chevron:before {
  left: 0;
  -webkit-transform: skew(0deg, 30deg);
          transform: skew(0deg, 30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  -webkit-transform: skew(0deg, -30deg);
          transform: skew(0deg, -30deg);
}

@-webkit-keyframes move {
  25% {
    opacity: 1;
  }
  33% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  67% {
    opacity: 1;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(50px) scale3d(0.5, 0.5, 0.5);
            transform: translateY(50px) scale3d(0.5, 0.5, 0.5);
  }
}

@keyframes move {
  25% {
    opacity: 1;
  }
  33% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  67% {
    opacity: 1;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(50px) scale3d(0.5, 0.5, 0.5);
            transform: translateY(50px) scale3d(0.5, 0.5, 0.5);
  }
}
@-webkit-keyframes pulse {
  to {
    opacity: 1;
  }
}
@keyframes pulse {
  to {
    opacity: 1;
  }
}



