.flippy-back {
    border-radius: 3px;
    background-color: #222;
    padding: 5px;
}

.flippy-back:after {
    position: absolute;
    content: "";
    top: calc(-1 * 3px);
    left: calc(-1 * 3px);
    z-index: -1;
    width: calc(100% + 3px * 3);
    height: calc(100% + 3px * 3);
    background: linear-gradient(
        60deg,
        hsl(224, 85%, 66%),
        hsl(269, 85%, 66%),
        hsl(314, 85%, 66%),
        hsl(359, 85%, 66%),
        hsl(44, 85%, 66%),
        hsl(89, 85%, 66%),
        hsl(134, 85%, 66%),
        hsl(179, 85%, 66%)
    );
    background-size: 300% 300%;
    background-position: 0 50%;
    border-radius: calc(3 * 3px);
    -webkit-animation: moveGradient 4s alternate infinite;
            animation: moveGradient 4s alternate infinite;
}

