.landing-test {
    width: 100%;
    height: auto;
    overflow: hidden;
    color: white;
    margin-bottom: 20%;
}
.test-container {
    background-image: linear-gradient(
            rgba(29, 21, 31, 0.6),
            rgba(29, 21, 31, 0.6)
        ),
        url(/static/media/testimonial_bg.07a5371f.png);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.testimonial-slider {
    color: white;
    width: 100%;
    height: 40vh;
    overflow: hidden;
}

.test-head {
    width: 100%;
    text-align: center;
    font-size: 40px;
    margin-bottom: 4%;
    font-weight: bolder;
    color: white;
}
@media (max-width: 950px) {
    .test-head {
        font-size: 1.7em !important;
    }
}

.test-img {
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
    float: left;
}

.test-text {
    width: 70%;
    float: left;
    padding-left: 10%;
}

.items {
    position: absolute;
    width: 100%;
    font-size: 1.4em;
    -webkit-animation-duration: 40s;
            animation-duration: 40s;
    -webkit-animation-timing-function: ease-in-out;
