.sidebar {
    z-index: 100000;
    position: fixed;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    top: 50%;
    left: 0;
    background: transparent;
}

@media only screen and (max-width: 950px) {
    .sidebar {
        display: none;
    }
}

.sidebaractive {
    border-right: 2px white solid;
}

.sidebar ul {
    height: 100vh;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sidebar ul li {
    position: relative;
    width: 70px;
    cursor: pointer;
    background: #92929265;
    text-transform: uppercase;
    transition: all 0.4s ease-out;
}

.sidebar ul li:first-child {
    border-top-right-radius: 20px;
}
.sidebar ul li:last-child {
    border-bottom-right-radius: 20px;
}

.sidebar ul li:after {
    position: absolute;
    background: black;
    color: #ffffff;
    top: 0;
    left: 70px;
    width: 70px;
    height: 100%;
    opacity: 0.5;
    -webkit-transform: perspective(400px) rotateY(90deg);
            transform: perspective(400px) rotateY(90deg);
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
    transition: all 0.4s ease-out;
}

.sidebar ul li:nth-child(1):after {
    content: "Home";
    line-height: 88px;

    border-top-right-radius: 20px;
}
.sidebar ul li:nth-child(2):after {
    content: "About";
    line-height: 88px;
}
.sidebar ul li:nth-child(3):after {
    content: "Perks";
    line-height: 85px;
}
.sidebar ul li:nth-child(4):after {
    content: "Work";
    line-height: 85px;
}
.sidebar ul li:nth-child(5):after {
    content: "Testimonials";
    font-size: 8px;
    line-height: 85px;
}
.sidebar ul li:nth-child(6):after {
    content: "Team";
    line-height: 70px;

    border-bottom-right-radius: 20px;
}

.sidebar ul li:hover {
    -webkit-transform: translateX(-70px);
            transform: translateX(-70px);
}

.sidebar ul li:hover:after {
    opacity: 1;
    -webkit-transform: perspective(400px) rotateY(0deg) scale(1);
            transform: perspective(400px) rotateY(0deg) scale(1);
}

.sidebar ul li > div {
    display: inline-block;
    padding: 25px 0;
    background: transparent;
}

.sidebar ul li div {
    position: relative;
}

.roof {
    width: 0;
    height: 0;
    top: 2px;
    border-style: solid;
    border-width: 0 21px 15px 21px;
    border-color: transparent transparent #ffffff transparent;
}

.roof-edge {
    position: absolute;
    z-index: 20;
    left: -17px;
    top: 3px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 17px 12px 17px;
    border-color: transparent transparent #ffffff transparent;
}
/*black triangle over red triangle*/
.roof-edge:after {
    position: absolute;
    left: -14.5px;
    top: 3px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 14.5px 10px 14.5px;
    border-color: transparent transparent #92929265 transparent;
}

.front {
    position: relative;
    top: 3px;
    width: 28.5px;
    height: 20px;
    margin: 0 auto;
    background: white;
}
/*door*/
.front:after {
    position: absolute;
    background: #92929265; /*here*/
    width: 11px;
    height: 13px;
    bottom: 0;
    left: 9px;
}

/*/// About me ////*/

.head {
    width: 32px;
    height: 35px;
    background: white;
    border-radius: 8px;
}
/*mouth*/
.head:after {
    width: 4px;
    height: 10px;
    background: white;
    position: absolute;
    border-radius: 4px 0 0 4px;
    top: 21px;
    left: 14px;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}

.eyes {
    width: 8px;
    height: 5px;
    border-radius: 50%;
    position: absolute;
    top: 10px;
    left: 5px;
    background: #92929265;
}
/*right eye*/
.eyes:after {
    width: 8px;
    height: 5px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 14px;
    background: #92929265;
}

.beard {
    width: 32px;
    height: 17px;
    background: #92929265;
    border: 2px solid white;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 0 0 8px 8px;
}
/*nose*/
.beard:after {
    position: absolute;
    top: -2px;
    left: 11px;
    background: white;
    width: 6px;
    height: 4px;
    border-left: 1px solid #92929265;
    border-right: 1px solid #92929265;
}

/*//work//*/

.paper {
    position: relative;
    height: 32px;
    width: 29px;
    background: white;
    border: 2px solid white;
}

/*window*/
.paper:after {
    position: absolute;
    top: 1px;
    left: 0;
    width: 25px;
    height: 29px;
    background: white;
    border-top: 4px solid #92929265;
}

.lines {
    position: absolute;
    top: 36px;
    left: 5px;
    width: 11px;
    box-shadow: 0 0 0 1px #92929265;
}

.lines:after {
    position: absolute;
    top: 4px;
    left: 3px;
    width: 14px;
    box-shadow: 0 0 0 1px #92929265;
}

.lines:nth-child(2) {
    position: absolute;
    top: 44px;
    left: 8px;
    width: 11px;
}

.lines:nth-child(2):after {
    position: absolute;
    top: 4px;
    left: -3px;
    width: 11px;
}

.lines:nth-child(3) {
    position: absolute;
    top: 52px;
    left: 8px;
    width: 14px;
}

.lines:nth-child(3):after {
    display: none;
}

/*//mail //*/

.mail-base {
    position: relative;
    width: 32px;
    height: 18px;
    background: white;
}

.mail-top {
    position: absolute;
    z-index: 20;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    border-style: solid;
    border-width: 0 16px 11px 16px;
    border-color: transparent transparent #92929265 transparent;
}

.mail-top:after {
    position: absolute;
    z-index: 20;
    left: -16px;
    top: 3px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 16px 9px 16px;
    border-color: transparent transparent white transparent;
}

.icons {
    color: white;
    font-size: 37px;
}
